import { Component, OnInit } from '@angular/core';
import { AuthService } from '../services/auth.service';
import { Router } from '@angular/router';
import { FormGroup, FormControl } from '@angular/forms';

@Component({
  selector: 'app-login-basic',
  templateUrl: './login-basic.component.html',
  styleUrls: ['./login-basic.component.scss']
})
export class LoginBasicComponent implements OnInit {

  ngOnInit(): void {
  }

  invalidCredentialMsg: string;
  constructor(private authService: AuthService, private router: Router) {
  }
  loginForm = new FormGroup({
    username: new FormControl(),
    password: new FormControl()
  });
  onFormSubmit() {
    let uname = this.loginForm.get('username').value;
    let pwd = this.loginForm.get('password').value;
    this.authService.isUserAuthenticated(uname, pwd).subscribe(
      authenticated => {
        if (authenticated) {
          let url = this.authService.getRedirectUrl();
          console.log('Redirect Url:' + url);
          this.router.navigate([url]);
        } else {
          this.invalidCredentialMsg = 'Invalid Credentials. Try again.';
        }
      }
    );
  }
}
